import React from 'react';
import'../Styles/Home.css';
import axios from 'axios';

class payment2 extends React.Component
{
    constructor()
    {
        super();
        this.state={
            name:'',
            phno:undefined
        }
    }

    componentDidMount=()=>
    {
        axios(
            {
                method:'GET',
                url:'http://localhost:5000/payerDetail',
                headers:{'Content-Type':'application/json'}
            }
        ).then(res=>
         {
             this.setState({payerDetails:res.data})
         })
         .catch(err=>console.log(err))
    }

    purchase=()=>
    {
        let filterObj={mid:"QGiEhd82527588670761",ORDERID:new Date().getTime(),key:"R5dpoOd30owvnfs&",website:"WEBSTAGING"};

        axios(
            {
                method:'POST',
                url:"http://localhost:5000/paynow",
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        )
        .then(res=>
            {
                this.setState({restaurants:res.data})
            })
        .catch(err=>console.log(err))
    }

    handleName=(event,state)=>
    {
        const name={[state]:event.target.value}
        this.setState(name);
        sessionStorage.setItem('name',name);
    }

    handlePhno=(event,state)=>
    {
        const phno={[state]:event.target.value}
        this.setState(phno);
        sessionStorage.setItem('phone',phno);
    }

    render()
    {
        const {name,phno}=this.state;
        const email=sessionStorage.getItem('email');
        const cost=sessionStorage.getItem('cost');
        return(
            <div>
                <div className="row my-5">
                    <div className="col-md-4 offset-md-4">
                        <div className="card">
                            <div className="card-body">
                                <form className="" action="/paynow" method="post">
                                    <div className="form-group">
                                        <label for="">Name: </label>
                                        <input className="form-control" type="text" name="name" value={name} onChange={(event)=>this.handleName(event,'name')} />
                                    </div>
                                    <div className="form-group">
                                        <label for="">Email: </label>
                                        <input className="form-control" type="text" name="email" value={email} disabled="true" />
                                    </div>
                                    <div className="form-group">
                                        <label for="">Phone: </label>
                                        <input className="form-control" type="text" name="phone" value={phno} onChange={(event)=>this.handlePhno(event,'phno')} />
                                    </div>
                                        <div className="form-group">
                                        <label for="">Amount: {cost} </label>
                                        <input className="form-control" type="text" name="amount" value={cost} disabled="true" />
                                    </div>
                                    <div className="form-group">
                                        <button className="btn form-control btn-primary" onclick={this.purchase}>Pay Now</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default payment2;