import React from 'react';
import'../Styles/Home.css';
import Wallpaper from './Wallpaper';
import QuickSearch from './QuickSearch';
//import Details from './Details';
import axios from 'axios';
import queryString from 'query-string';

class Home extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            locations:[],
            tiles:[],
            id:[],
            image:undefined
        }
    }
    
    componentDidMount=()=>
    {
        
        //sessionStorage.setItem('location_id',undefined);

       axios(
           {
               method:'GET',
               url:'http://localhost:5000/hello/getLocation',
               headers:{'Content-Type':'application/json'}
           }
       ).then(res=>
        {
            this.setState({locations:res.data.locations})
        })
        .catch(err=>console.log(err))

       axios(
           {
               method:'GET',
               url:'http://localhost:5000/world/tile',
               headers:{'Content-Type':'application/json'}
           }
       ).then(res=>
        {
            this.setState({tiles:res.data.restaurants,image:res.data.restaurants.image})
        })
        .catch(err=>console.log(err))
    }
    
    render()
    {
        const {locations,tiles,id,image}=this.state;
        return(
            <div>
                <Wallpaper locations={locations}/>
                <QuickSearch tiles={tiles} image={image}/>
            </div>
        )
    }
}

export default Home;