import React from 'react';
import{withRouter} from 'react-router-dom';
import'../Styles/Home.css';
import Filter from './Filter';
import queryString from 'query-string';
//import breakfast from '../images';
//import axios from 'axios';

class QuickSearch extends React.Component
{
   
    
    handleClick=(event) =>
    {
        //this.mealtypeId={mealtypeId:[]};
        //mealtypeId=this.setState({mealtype_id:mealtypeId})
        const Id=event.target.id;
        //const location_id=sessionStorage.getItem('location_id');
        //alert(id);
        const queryParams=queryString.parse(this.props.location.search);
        const location_id=queryParams.location_id;
        if(location_id == undefined)
            this.props.history.push(`/filter/?mealtype_id=${Id}`);
        else
        this.props.history.push(`/filter/?mealtype_id=${Id}&location_id=${location_id}`);
    }
    

    render()
    {
        
        const {tiles}=this.props;
       
        //const {mealtype_id}=this.state;
        return(
            <div>  
                <div className="class4"><h2>Quick Searches</h2></div>
                <div className="class5"><h5>Discover restaurants by type of meal</h5></div>
        
       
                <div className="container-fluid">
                    <div className="row" style={{margin:'20px 0px 0px 0px'}}>
                        {tiles.map((item)=>{
                            
                            return  <div className="col-sm-4 col-md-4 col-lg-4" id={item.mealtype_id} onClick={this.handleClick} >
                            <div className="item1" id={item.mealtype_id} onClick={this.handleClick}>
                                
                                <img src={require('../' + item.image).default} alt="unable to load" width="40%" height="150px" id={item.mealtype_id} onClick={this.handleClick}/>
                                <div className="text1" id={item.mealtype_id} onClick={this.handleClick}>
                                    <p className="text_style" id={item.mealtype_id} onClick={this.handleClick}>{item.name}</p>
                                    <p id={item.mealtype_id} onClick={this.handleClick}>Start your day with exclusive breakfast options</p>
                                    
                                </div>
                            </div> 
                    
                        </div> 
                        })}
                    </div>
                </div>     
               
            </div>            
        
        )}
}

export default withRouter(QuickSearch);
