import axios from 'axios';
import Select from 'react-select';
import React,{useState} from 'react';
//import{withRouter} from 'react-router-dom';
import '../Styles/Filter.css';
import queryString from 'query-string';
import Details from './Details';
import breakfast from '../images/breakfast.png';
import Location from './Location';
import header from './Header';

class cartPage extends React.Component
{
    constructor()
    {
        super();
        this.state={
            orders:[],
            qty:0      
        }        
    }
    
    componentDidMount=(event)=>
    {
        const email=sessionStorage.getItem('email');
        let filterObj={email:email};
        axios(
            {
              method:'POST',
              url:`http://localhost:5000/userCartDetails`,
              headers:{'Content-Type':'application/json'},
              data:filterObj
            }
          ).then(res=>
              {
                  this.setState({orders:res.data.orders})
              })
              .catch(err=>console.log(err))
    }   

    purchase=(price1,qty2,food_id,food_name)=>
    {
        const price=Number(price1) * Number(qty2);
        const cost=price.toString();
        sessionStorage.setItem('cost',cost);
        
        this.props.history.push('/payment');
    }

    deleteCartItem=(food_id)=>
    {
        const email=sessionStorage.getItem('email');
        let filterObj={email:email,food_id:food_id};
        let filterObj2={email:email};
        axios(
            {
              method:'POST',
              url:`http://localhost:5000/deleteCartItem`,
              headers:{'Content-Type':'application/json'},
              data:filterObj
            }
          ).then(res=>
              {
                  this.setState({orders:res.data.orders})
              })
              .catch(err=>console.log(err))

        axios(
        {
            method:'POST',
            url:`http://localhost:5000/userCartDetails`,
            headers:{'Content-Type':'application/json'},
            data:filterObj2
        }
        ).then(res=>
            {
                this.setState({orders:res.data.orders})
            })
            .catch(err=>console.log(err))
    }

    render()
    {
        const {orders,costArray,i}=this.state;
        let {qty}=this.state;
        let qty2=sessionStorage.getItem('value2');
        let count=sessionStorage.getItem('count');
        qty = qty2;

        return(
            
        <div>
            {count.length >= 1 ? 
            <div className="container">
                <div className="row">
                <div className="col-sm-8 col-md-8 col-lg-8">
                    {orders && orders.length > 0 ? orders && orders.map(item=> item.orders.map(item2 => {
                    return (
                    <div className="Item">
                        <div className="row pl-1">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="img" src={breakfast} />
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest-name" id={item2.food_id} onClick={this.handleDetail}>{item2.food_name}</div>
                                <div className="res-location">Item Price : {'\u20B9'} {item2.item_cost}</div>
                                <div className="res-location">Quantity : {item2.qty}</div>
                                <div> <button type="button" className="order-now" onClick={()=>this.purchase(`${item2.item_cost}`,`${item2.qty}`,`${item2.food_id}`,`${item2.food_name}`)}> <h6>ORDER NOW</h6> </button>  </div>
                                <div style={{marginTop:"-45px",marginLeft:"125px"}}> <button type="button" style={{color:"white",backgroundColor:"green", marginTop:"10px", borderColor:"green",border:"none"}} onClick={()=>this.deleteCartItem(`${item2.food_id}`)}> <h6>Remove from Cart</h6> </button>  </div>
                                {/*<button type="button" onClick={this.increment}> + </button>
                                <span>{qty}</span>
                                <button type="button" onClick={this.decrement}> - </button>*/}
                            </div>
                        </div>
                        <hr />
                        <div className="row padding-left">
                            <div className="col-sm-12 col-md-12 col-lg-12">
                                <div className="rest-address"><div className="className1">TOTAL COST : {'\u20B9'} {item2.item_cost * item2.qty} </div></div>
                                
                            </div>
                        </div> 
                    </div>)})) : <div>No Orders found</div>}
                </div>
            </div>
            </div> : null }
        </div>            
        )
    }   
}

export default cartPage;