import axios from 'axios';
import Select from 'react-select';
import React,{useState} from 'react';
//import{withRouter} from 'react-router-dom';
import '../Styles/Filter.css';
//import './QuickSearch';
import queryString from 'query-string';
import Details from './Details';
import breakfast from '../images/breakfast.png';
import Location from './Location';
import header from './Header';

class checking3 extends React.Component
{
    constructor()
    {
        super();
        this.state={
            restaurants:[],
            cuisineArray:[]
        }        
    }
    
    componentDidMount=()=>
    {
        axios(
            {
                method:'GET',
                url:'http://localhost:5000/getAllCuisine2',
                headers:{'Content-Type':'application/json'}
            }
        )
        .then(res=>
            {
                this.setState({restaurants:res.data.restaurants});
            })
        .catch(err=>console.log(err)) 
    }

    handleCuisineChange=(event,cuisine_id)=>
    {
        const {cuisineArray}=this.state;
       
        if (cuisineArray.indexOf(cuisine_id) == -1)
        {
            cuisineArray.push(cuisine_id);
        }
        else
        {
            var index=cuisineArray.indexOf(cuisine_id);
            cuisineArray.splice(index,1);
        }    

        const cuisine_id1=cuisineArray[0];
        let cuisine_id2=cuisineArray[1];
        let cuisine_id3=cuisineArray[2];

        if (cuisineArray[1] == undefined)
        {
            cuisine_id2=cuisineArray[0];
        }
        if (cuisineArray[2] == undefined)
        {
            cuisine_id3=cuisineArray[0];
        }

        this.props.history.push(`/checking3?cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}`);
        let filterObj={cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3};
        axios(
            {
                method:'POST',
                url:`http://localhost:5000/getAllCuisine`,
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        )
        .then(res=>
            {
                this.setState({restaurants:res.data.restaurants});
            })
        .catch(err=>console.log(err))
    }

    render()
    {
        const {restaurants}=this.state;
        return(
            
                <div>
                   
                            <input type="checkbox" name="cuisines" id={"4"} onChange={(event) => this.handleCuisineChange(event,"1")}  />
                            <span className="checkbox-items" > North Indian </span> <div style={{display: "block"}}> </div>
                            <input type="checkbox" name="cuisines"  id={"5"} onChange={(event) => this.handleCuisineChange(event,"2")}  />
                            <span className="checkbox-items" > south indian </span> <div style={{display: "block"}}> </div>
                            <input type="checkbox" name="cuisines" id={"6"} onChange={(event) => this.handleCuisineChange(event,"3")}  />
                            <span className="checkbox-items" > chinese </span> <div style={{display: "block"}}> </div>
                         
                <div className="col-sm-8 col-md-8 col-lg-8">
                    {restaurants && restaurants.length > 0 ? restaurants && restaurants.map(item=> {
                    return (
                    <div className="Item">
                        <div className="row pl-1">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="img" src={breakfast} />
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest-name" id={item.location_id} onClick={this.handleDetail}>{item.restaurant}</div>
                                <div className="res-location">FORT</div>
                                <div className="rest-address">Hyderabad</div>
                            </div>
                        </div>
                        <hr />
                        <div className="row padding-left">
                            <div className="col-sm-12 col-md-12 col-lg-12">
                                <div className="rest-address"><div className="className1">CUISINES : {item.cuisine.map(item2=>item2.name)}</div></div>
                                <div className="rest-address"><div className="className1">COST FOR TWO : {'\u20B9'} 2000 </div></div>
                            </div>
                        </div> 
                    </div>)}) : <div>No Data found</div>}
                </div>
            </div>            
        )
    }
}

export default checking3;