import React from 'react';
import '../Styles/Filter.css';
import Modal from 'react-modal';
import{withRouter} from 'react-router-dom';
import axios from 'axios';

const customStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)'
    }
  };


class Header extends React.Component
{
    constructor()
    {
        super();
        this.state=
        {
            signUpModalIsOpen:false,
            loginModalIsOpen:false,
            errorLoginModalIsOpen:false,
            email:undefined,
            password:undefined,
            firstName:undefined,
            lastName:undefined,
            i:0,
            users:[],
            count:'h',
            countUser:0
        }
    }

    //var filterObj=[{email:'',password:'',firstName:'',lastName:''}];

    random=()=>
    {
    //let filterObj=[{email:'',password:'',firstName:'',lastName:''}];
    }

    handleLogo=()=>
    {
        this.props.history.push('/');
    }

    login=()=>
    {
        this.setState({loginModalIsOpen:true});
    }
  
    signup=()=>
    {
        this.setState({signUpModalIsOpen:true});
    }

    addUser=(event)=>
    {
        const email=event.target.id;
        const password=event.target.value;
        const firstName=this.state.firstName;
        const lastName=this.state.lastName ? this.state.lastName : undefined;
        let filterObj={email:email,password:password,firstName:firstName,lastName:lastName};
        let filterObj2={email:email};
        let count100=1;
        
        axios(
            {
                method:'POST',
                url:'http://localhost:5000/checkuser',
                headers:{'Content-Type':'application/json'},
                data:filterObj2
            }
        )
        .then(result => 
            {
                this.setState({users:result.data.users})
                if(result.data.users.length > 0)
                {
                    alert("Email already exists");
                    this.setState({signUpModalIsOpen:false});
                }

                else
                {                
                axios(
                    {
                        method:'POST',
                        url:'http://localhost:5000/signup',
                        headers:{'Content-Type':'application/json'},
                        data:filterObj
                    }
                )
                .then(res =>
                    {
                        this.setState({users:res.data.users})
                    })
                    .catch(err=>console.log(err))
                    if (email != undefined && email != '' && password != undefined && password != '' && firstName != undefined && firstName != '')
                    {
                        this.setState({signUpModalIsOpen:false});
                        alert("user added successfully");
                    }
                    else
                    {
                        alert("Please add Email,Password and First Name mandatorily");
                    }
                }
            })
            .catch(err => console.log(err))
        
    }

    handleEmail=(event,state)=>
    {
        //let arr=[{email:email},{password:password},{firstName:firstName},{lastName:lastName}];
        //const {i,arr}=this.state;
        const email={[state]:event.target.value};
        this.setState(email);        
    }

    handlePassword=(event,state)=>
    {
        const password={[state]:event.target.value};
        this.setState(password);
    }

    handleFN=(event,state)=>
    {
        const firstName={[state]:event.target.value};
        this.setState(firstName);
    }

    handleLN=(event,state)=>
    {
        const lastName={[state]:event.target.value};
        this.setState(lastName);
    }

    openLogin=(event)=>
    {
        //const {count}=this.state;
        const email=event.target.id;
        const password=event.target.value;
        let filterObj={email:email,password:password};
        axios(
            {
                method:'POST',
                url:'http://localhost:5000/login',
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        )
        .then(res=>
            {
                this.setState({users:res.data.users});
                let name=res.data.users.map(item => item.firstName);
                if(res.data.users.length >= 1)
                {
                    this.state.count='hi';
                    this.setState({loginModalIsOpen:false});
                    this.setState({errorLoginModalIsOpen:false});
                    //sessionStorage.setItem('count',this.state.count);
                }
                
            })
        .catch(err=>
            {
                this.setState({errorLoginModalIsOpen:true})
                //alert("invalid credentials");
            })
        
    }

    openLoginErrorModal=(event)=>
    {
        //const {count}=this.state;
        const email=event.target.id;
        const password=event.target.value;
        let filterObj={email:email,password:password};
        axios(
            {
                method:'POST',
                url:'http://localhost:5000/login',
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        )
        .then(res=>
            {
                this.setState({users:res.data.users});
                let name=res.data.users.firstName;
                if(res.data.users.length >= 1)
                {
                    this.state.count='hi';
                    this.setState({errorLoginModalIsOpen:false});
                   // this.setState({loginModalIsOpen:false});
                    //this.setState({errorLoginModalIsOpen:false});
                    //sessionStorage.setItem('count',this.state.count);
                }
            })
        .catch(err=>
            {
                this.setState({errorLoginModalIsOpen:true})
            })
        
    }

    cancel=()=>
    {
        this.setState({signUpModalIsOpen:false});
        this.setState({loginModalIsOpen:false});
        this.setState({errorLoginModalIsOpen:false});
    }

    handleEmailLogin=(event,state)=>
    {
        const email={[state]:event.target.value};
        this.setState(email);
        
    }

    handlePwdLogin=(event,state)=>
    {
        const password={[state]:event.target.value};
        this.setState(password);
        
    }    

    logout=()=>
    {
        this.state.count='h';
        this.setState({count:this.state.count});
        alert("logged out successfully");
        this.props.history.push('/');
    }

    handleUser=(event)=>
    {
        const countUser=event.target.id;
        this.state.countUser=1;
    }

    gotoOrders=()=>
    {
        //this.props.history.push('/cartPage');
        this.props.history.push('/ordersPage');
    }

    myOrders=()=>
    {
        this.props.history.push('/cartPage2');
    }

    render()
    {
        const {signUpModalIsOpen,loginModalIsOpen,email,password,firstName,lastName,users,errorLoginModalIsOpen,count,countUser}=this.state;
        sessionStorage.setItem('count',count);
        return(

        <div>
            {this.state.count.length == 1 ? <div>
                <div className="band"></div>
                <button type="button" className="login" onClick={this.login}>Login</button>
                <button type="button" className="register2" onClick={this.signup}>Create account</button>
                <div className="logo" onClick={this.handleLogo}>
               <div className="letter" onClick={this.handleLogo}> <h2 onClick={this.handleLogo}>e!</h2> </div>
            
                </div>  </div>
                : 
                <div>
                <div className="band" style={{marginBottom:"-20px"}}></div>

                    <div class="dropdown">
                        
                        <button type="button" className="glyphicon glyphicon-user register dropbtn" id={this.state.countUser} onClick={this.handleUser}>{users.map(item=>{return item.firstName} )} {users.map(item=>sessionStorage.setItem('email',`${item.email}`) )}</button>
                        
                        <div class="dropdown-content">
                        <span> <button className="user" onClick={this.gotoOrders}>My Orders</button> </span>
                            <span> <button className="user" onClick={this.logout}>Sign Out</button> </span>
                        </div>
                    </div>
                    <button type="button" className="register2 glyphicon glyphicon-shopping-cart cart-style" onClick={this.myOrders}></button>
                
                <div className="logo" onClick={this.handleLogo}>
                <div className="letter" onClick={this.handleLogo}> <h2 onClick={this.handleLogo}>e!</h2> </div>
                {/*{this.state.countUser == 1 ?
                    <div style={{width:"50px",height:"70px"}}> Logout</div>
                : null } */}
                </div>
                </div>
                
            }

        <Modal
          isOpen={signUpModalIsOpen}
          style={customStyles}
          
        >
            <div style={{textAlign:"center",paddingBottom:"10px"}}> <h3>SignUp</h3>   </div>
            <div>
                <div style={{marginTop:"10px"}}>  <label>Email   </label>   </div>
                <div style={{marginLeft:"61px",marginTop:"-28px"}}>
                    <input className="modal-box" style={{paddingLeft:"4px",marginLeft:"19px"}} autocomplete="new-text" type="text" value={email} onChange={(event)=>this.handleEmail(event,'email')}/>
                </div>
            </div>
            <div>
                <label>Password   </label>
                <input className="modal-box" style={{paddingLeft:"3px",marginLeft:"18px"}} autocomplete="new-password" type="password" value={password} onChange={(event)=>this.handlePassword(event,'password')}/>
            </div>
            <div>
                <label>First Name   </label>
                <input className="modal-box" type="text" value={firstName} onChange={(event)=>this.handleFN(event,'firstName')}/>
            </div>
            <div>
                <label>Last Name   </label>
                <input className="modal-box" style={{paddingLeft:"4px",marginLeft:"12px"}} type="text" value={lastName} onChange={(event)=>this.handleLN(event,'lastName')}/>
            </div>
            <div className="modal-button2">  <button className="btn btn-sm btn-primary login-button" id={email} value={password} onClick={this.addUser}>Confirm</button>  </div>
            <div className="modal-button2">  <button className="btn btn-sm btn-primary login-button" style={{backgroundColor:"red",marginTop:"10px",borderColor:"red",paddingRight:"111px"}} onClick={this.cancel}>Cancel</button>  </div>
        </Modal>

        <Modal
          isOpen={loginModalIsOpen}
          style={customStyles}
          
        >
            <div style={{textAlign:"center",paddingBottom:"10px"}}> <h3>Login User</h3>   </div>
            <div>
                <div style={{marginTop:"10px"}}>  <label>Email   </label>   </div>
                <div style={{marginLeft:"61px",marginTop:"-28px"}}>
                    <input className="modal-box" type="text" value={email} onChange={(event)=>this.handleEmailLogin(event,'email')}/>
                </div>
            </div>
            <div>
                <label>Password   </label>
                <input className="modal-box" type="password" value={password} onChange={(event)=>this.handlePwdLogin(event,'password')} />
            </div>

            <div className="modal-button"> <button className="btn btn-sm btn-primary login-button" id={email} value={password} onClick={this.openLogin}>Login</button> </div>
            <div className="modal-button"> <button className="btn btn-sm btn-primary login-button" style={{backgroundColor:"red",marginTop:"10px",borderColor:"red",paddingRight:"98px"}} onClick={this.cancel}>Cancel</button>  </div>
            
        </Modal>

        <Modal
          isOpen={errorLoginModalIsOpen}
          style={customStyles}
          
        >   
            <div style={{textAlign:"center",paddingBottom:"10px"}}> <h3>Login User</h3>   </div>
            <div>
                <div style={{color:"red"}}>Invalid credentials</div>
                <div style={{marginTop:"10px"}}>  <label>Email   </label>   </div>
                <div style={{marginLeft:"61px",marginTop:"-28px"}}>
                    <input className="modal-box" type="text" value={email} onChange={(event)=>this.handleEmailLogin(event,'email')}/>
                </div>
            </div>
            <div>
                <label>Password   </label>
                <input className="modal-box" type="password" value={password} onChange={(event)=>this.handlePwdLogin(event,'password')} />
            </div>

            <div className="modal-button"> <button className="btn btn-sm btn-primary login-button" id={email} value={password} onClick={this.openLogin}>Login</button> </div>
            <div className="modal-button"> <button className="btn btn-sm btn-primary login-button" style={{backgroundColor:"red",marginTop:"10px",borderColor:"red",paddingRight:"98px"}} onClick={this.cancel}>Cancel</button>  </div>
        </Modal>
    </div>
            
    
        )
    }
}

export default withRouter(Header);