import React from 'react';
import'../Styles/Home.css';
import axios from 'axios';

class paynow extends React.Component
{
    constructor()
    {
        super();
        this.state={
            name:'',
            phno:undefined
        }
    }

    componentDidMount=()=>
    {
        const amount=sessionStorage.getItem('cost');
        const email=sessionStorage.getItem('email');
        const name=sessionStorage.getItem('name');
        const phone=sessionStorage.getItem('phone');
        let filterObj={amount:amount,customerId:name,customerEmail:email,customerPhone:phone};

        axios(
            {
                method:'POST',
                url:'http://localhost:5000/paynow',
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        ).then(res=>
         {
             this.setState({payerDetails:res.data})
         })
         .catch(err=>console.log(err))
    }
}

export default paynow;