import axios from 'axios';
import Select from 'react-select';
import React,{useState} from 'react';
import '../Styles/Filter.css';
import Modal from 'react-modal';
import queryString from 'query-string';
import Details from './Details';
import breakfast from '../images/breakfast.png';
import Location from './Location';
import header from './Header';

const customStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)'
    }
  };

class payment extends React.Component
{
    constructor()
    {
        super();
        this.state={
            signUpModalIsOpen:false,
            isLoginAlertModal:false,
            count:0,
            email:undefined,
            password:undefined,
            firstName:undefined,
            lastName:undefined,
            orderCount:"0"                  
        }        
    }
    

    componentDidMount=(event)=>
    {
        
    }   

   handleBank=(event)=>
   {
    const bank_id=event.target.id;
    const {count}=this.state;
    this.setState({count:count+1});
    sessionStorage.setItem('bank_id',bank_id);
   }

   bankPage=()=>
   {
       const bank_id=sessionStorage.getItem('bank_id');
       const {count}=this.state;
       if (count == 1)
       {
        if (bank_id == "1")
            window.location='https://netbanking.hdfcbank.com/netbanking/?_ga=2.140794674.1630067090.1606319093-822697684.1606319093'
        else if (bank_id == "2")
        {
            window.location= "https://retail.onlinesbi.com/retail/login.htm"
        }
        else if (bank_id == "3")
        {
            window.location="https://retail.axisbank.co.in/wps/portal/rBanking/axisebanking/AxisRetailLogin/!ut/p/a1/04_Sj9CPykssy0xPLMnMz0vMAfGjzOKNAzxMjIwNjLwsQp0MDBw9PUOd3HwdDQwMjIEKIoEKDHAARwNC-sP1o_ArMYIqwGNFQW6EQaajoiIAVNL82A!!/dl5/d5/L2dBISEvZ0FBIS9nQSEh/?_ga=2.116442501.2087974211.1606319563-2005825147.1606319563"
        }
        else if (bank_id == "4")
        {
            window.location="https://indusnet.indusind.com/corp/BANKAWAY?Action.RetUser.Init.001=Y&AppSignonBankId=234&AppType=corporate&CorporateSignonLangId=001"

        }
        else
        {
            window.location="https://feba.bobibanking.com/corp/AuthenticationController?FORMSGROUP_ID__=AuthenticationFG&__START_TRAN_FLAG__=Y&FG_BUTTONS__=LOAD&ACTION.LOAD=Y&AuthenticationFG.LOGIN_FLAG=1&BANK_ID=012&AuthenticationFG.USER_TYPE=1&AuthenticationFG.MENU_ID=CIMSHP&AuthenticationFG.CALL_MODE=2&RU=null&CATEGORY_ID=TPN&QS=AZjAAEqTKfmJVz9fJLkoQ%2BMKilHQ4dKBOKCMbXeACJMBmMAASpMp%2BYlXP18kuShDZdsqFit1dAjidBHAJX%2BffUjnxnzHpSgS14lamxR1mAq5%2Be0CceIyIDDWo9rSd0Jc%0D%0AiFpJ91KuqXON1QWISkb%2FYVvSCHcWa%2BDlWD1KGhDTJdqHs5dWX%2F1yF7dmM8I79arBAZjAAEqTKfmJVz9fJLkoQw%2BgUhF2KoBPjC3XhOh9AF92hY5mRefOQyG3CHU7czymIeGG%0D%0ALg%2B5UUPpCt1dRtMbt223UZwJw0xZDgiCHzQtNug%3D"
        }
       }
    else
    {
        this.setState({isLoginAlertModal:true})
    }
   }

   modal=()=>
   {
       const {count}=this.state;
       if (count >= 1)
       {
        this.setState({signUpModalIsOpen:true})
       }
       else
       {
        this.setState({isLoginAlertModal:true})
       }
   }

   cancel=()=>
   {
       const {count}=this.state;
    this.setState({signUpModalIsOpen:false});
   }

   cancelLoginModal=()=>
   {
    this.setState({isLoginAlertModal:false})
   }

   handleEmail=(event,state)=>
    {
        const email={[state]:event.target.value};
        this.setState(email);        
    }

    handlePassword=(event,state)=>
    {
        const password={[state]:event.target.value};
        this.setState(password);
    }

    handleFN=(event,state)=>
    {
        const firstName={[state]:event.target.value};
        this.setState(firstName);
    }

    handleLN=(event,state)=>
    {
        const lastName={[state]:event.target.value};
        this.setState(lastName);
    }

    addToOrders=()=>
    {
        const {orderCount}=this.state;
        this.setState({signUpModalIsOpen:false});
       alert("Your order placed successfully");
       this.setState({orderCount:"1"});
       sessionStorage.setItem('orderCount',orderCount);
       const email=sessionStorage.getItem('email');
       const food_id=sessionStorage.getItem('foodIdFromCart');
       const food_name=sessionStorage.getItem('foodNameFromCart');
       const item_cost=Number(sessionStorage.getItem('itemCostFromCart'));
       const cost=Number(sessionStorage.getItem('cost'));
       const qty=Number(sessionStorage.getItem('qtyFromCart'));
       const restaurant_id=sessionStorage.getItem('restaurant_id');
       const restaurant=sessionStorage.getItem('restaurant');
       const dateAndTime=new Date().toLocaleString();
       let filterObj={email:email,food_id:food_id,food_name,item_cost:item_cost,cost:cost,qty:qty,restaurant_id:restaurant_id,restaurant:restaurant,dateAndTime:dateAndTime};
       let filterObj2={email:email,food_id:food_id,food_name,item_cost:item_cost,cost:cost,qty:qty,restaurant_id:restaurant_id,restaurant:restaurant};
       
       axios(
        {
          method:'POST',
          url:`http://localhost:5000/userOrders`,
          headers:{'Content-Type':'application/json'},
          data:filterObj
        }
      ).then(res=>
          {
              this.setState({orders:res.data.orders})
          })
          .catch(err=>console.log(err))
        axios(
        {
            method:'POST',
            url:`http://localhost:5000/deleteCartItem`,
            headers:{'Content-Type':'application/json'},
            data:filterObj2
        }
        ).then(res=>
            {
                this.setState({orders:res.data.orders})
            })
            .catch(err=>console.log(err))
    }

    render()
    {
        const {signUpModalIsOpen,isLoginAlertModal,count,email,password,firstName,lastName}=this.state;
        const cost=sessionStorage.getItem('cost');
        return(
            
        <div>
            <div style={{marginLeft:"30px"}}> <h1>BillDesk</h1> </div>
            <div>
                <div style={{marginLeft:"20px"}}> <input type="radio" name="bank" id={"1"} onChange={this.handleBank} /> <span>  HDFC Bank </span> </div>
                <div style={{marginLeft:"20px"}}> <input type="radio" name="bank" id={"2"} onChange={this.handleBank} />  <span>  SBI Bank</span> </div>
                <div style={{marginLeft:"20px"}}> <input type="radio" name="bank" id={"3"} onChange={this.handleBank}/> <span>  AXIS Bank</span> </div>
                <div style={{marginLeft:"20px"}}> <input type="radio" name="bank" id={"4"} onChange={this.handleBank}/> <span>  IndusInd Bank</span> </div>
                <div style={{marginLeft:"20px"}}> <input type="radio" name="bank" id={"5"} onChange={this.handleBank}/> <span>  Bank of Baroda</span> </div>
            </div>
            <div>
                <div style={{marginLeft:"20px",marginTop:"10px"}}> <button type="button" onClick={this.modal}>Debit Card</button> </div>
                <div style={{marginLeft:"20px",marginTop:"10px"}}> <button type="button" onClick={this.modal}>Credit Card</button> </div>
                <div style={{marginLeft:"20px",marginTop:"10px"}}> <button type="button" onClick={this.bankPage}>Net Banking</button> </div>
            </div>
        
            <div>
            <Modal
          isOpen={signUpModalIsOpen}
          style={customStyles}
          
        >
            <div style={{textAlign:"center",paddingBottom:"10px"}}> <h3>Payment Order</h3>   </div>
            <div>
                <div style={{marginTop:"10px"}}>  <label>Card No.   </label>   </div>
                <div style={{marginLeft:"61px",marginTop:"-28px"}}>
                    <input className="modal-box" style={{paddingLeft:"4px",marginLeft:"42px"}} autocomplete="new-text" type="text" onChange={(event)=>this.handleEmail(event,'email')}/>
                </div>
            </div>
            <div>
                <label>Valid Thru   </label>
                <input className="modal-box" style={{paddingLeft:"3px",marginLeft:"37px"}} autocomplete="new-password" type="date" onChange={(event)=>this.handlePassword(event,'password')}/>
            </div>
            <div>
                <label>Name on Card   </label>
                <input className="modal-box" type="text" onChange={(event)=>this.handleFN(event,'firstName')}/>
            </div>
            <div>
                <label>CVV   </label>
                <input className="modal-box" style={{paddingLeft:"4px",marginLeft:"76px"}} type="password" onChange={(event)=>this.handleLN(event,'lastName')}/>
            </div>
            <div> <h5> Amount Payable : {'\u20B9'} {cost} </h5> </div>
            <div className="modal-button2">  <button className="btn btn-sm btn-primary login-button" onClick={this.addToOrders}>Confirm</button>  </div>
            <div className="modal-button2">  <button className="btn btn-sm btn-primary login-button" style={{backgroundColor:"red",marginTop:"10px",borderColor:"red",paddingRight:"111px"}} onClick={this.cancel}>Cancel</button>  </div>
        </Modal> </div>
    
        <div>
            <Modal
            isOpen={isLoginAlertModal}
            style={customStyles}  

            >
            <h5>Please select a bank to make payment</h5>
            <button className="btn btn-sm btn-primary"  onClick={this.cancelLoginModal}>Cancel</button>   
            
            </Modal> 
        </div> 
            
        </div>            
        )
    }   
}

export default payment;