import axios from 'axios';
import React from 'react';
import{withRouter} from 'react-router-dom';
import '../Styles/Filter.css';
import queryString from 'query-string';
import breakfast from '../images/breakfast.png';

class Filter extends React.Component
{
    constructor()
    {
        super();
        this.state={
            restaurants:[],
            totalPages:[],
            location_id:undefined,
            cuisine_id:undefined,
            mealtype_id:undefined,
            hcost:10000,
            lcost:1,
            page:undefined,
            sort:1,
            locations:[],
            cuisineArray:[],
            arr:[]
        }
        var i=5;   
    }    

    componentDidMount=(event)=>
    {
        const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        const mealtype_id=queryParams.mealtype_id;
        const location_id=queryParams.location_id ? queryParams.location_id : undefined;
        let filterObj={};  

        if(location_id != undefined)
        {
            filterObj={mealtype_id:mealtype_id,location_id:location_id};
        }

        else
        {
            filterObj={mealtype_id:mealtype_id};
        }

        axios(
            {
                method:'POST',   
                url:'http://localhost:5000/restaurantfilter',     
                headers:{'Content-Type':'application/json'},
                data:filterObj  
            }
        ).then(res=>
            {
                this.setState({restaurants:res.data.restaurants,totalPages:res.data.totalPages})
            })
            .catch(err=>console.log(err))

            axios(
                {
                    method:'GET',   
                    url:'http://localhost:5000/hello/getLocation',      
                    headers:{'Content-Type':'application/json'},
                    data:filterObj  
                }
            ).then(res=>
                {
                    this.setState({locations:res.data.locations})
                })
                .catch(err=>console.log(err))

             axios(
                {
                    method:'GET',   
                    url:'http://localhost:5000/abbo/getLocation', 
                    headers:{'Content-Type':'application/json'},
                    
                }
            ).then(res=>
                {
                    this.setState({cuisines:res.data.cuisines})
                })
                .catch(err=>console.log(err))
    }
    
costChange=(lcost,hcost)=>
{
    const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
    const mealtype_id=queryParams.mealtype_id;
    const sort=queryParams.sort ? queryParams.sort : 1;
    const location_id=queryParams.location_id;
    const cuisine_id1=queryParams.cuisine_id1;
    const cuisine_id2=queryParams.cuisine_id2;
    const cuisine_id3=queryParams.cuisine_id3;
    const cuisine_id4=queryParams.cuisine_id4;
    const cuisine_id5=queryParams.cuisine_id5;
    let filterObj={};

if (cuisine_id1 == undefined)
{
    if (location_id != undefined && location_id != "Select")
    {
    filterObj={lcost:lcost,hcost:hcost,mealtype_id:mealtype_id,sort:sort,location_id:location_id};
    this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${sort}&hcost=${hcost}&lcost=${lcost}&location_id=${location_id}`);
    }

    else
    {
    filterObj={lcost:lcost,hcost:hcost,mealtype_id:mealtype_id,sort:sort};
    this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${sort}&hcost=${hcost}&lcost=${lcost}`);
    }
    axios(
    {
        method:'POST',
        url:'http://localhost:5000/restaurantfilter',
        headers:{'Content-Type':'application/json'},
        data:filterObj
    }
    )
    .then(res=>
    {
        this.setState({restaurants:res.data.restaurants})
    })
} //end of 1st if
else
{
    if (location_id != undefined && location_id != "Select")
    {
    filterObj={lcost:lcost,hcost:hcost,mealtype_id:mealtype_id,sort:sort,location_id:location_id,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5};
    this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${sort}&hcost=${hcost}&lcost=${lcost}&location_id=${location_id}&cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}`);
    }

    else
    {
    filterObj={lcost:lcost,hcost:hcost,mealtype_id:mealtype_id,sort:sort,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5};
    this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${sort}&hcost=${hcost}&lcost=${lcost}&cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}`);
    }
    axios(
    {
        method:'POST',
        url:`http://localhost:5000/cuisineHandler2`,
        headers:{'Content-Type':'application/json'},
        data:filterObj
    }
    )
    .then(res=>
        {
            this.setState({restaurants:res.data.restaurants});
        })
    .catch(err=>console.log(err))
  } //end of 1st else
    
}

    sortChange=(id)=>
    {
        const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        const mealtype_id=queryParams.mealtype_id;
        const hcost=queryParams.hcost ? queryParams.hcost : 10000;
        const lcost=queryParams.lcost ? queryParams.lcost : 1;
        const location_id=queryParams.location_id;
        const cuisine_id1=queryParams.cuisine_id1;
        const cuisine_id2=queryParams.cuisine_id2;
        const cuisine_id3=queryParams.cuisine_id3;
        const cuisine_id4=queryParams.cuisine_id4;
        const cuisine_id5=queryParams.cuisine_id5;
        let filterObj={};

      if (cuisine_id1 == undefined)
      {
        if(location_id == undefined || location_id == 'Select')
        {
        filterObj={sort:id,mealtype_id:mealtype_id,hcost:hcost,lcost:lcost};
        this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${id}&hcost=${hcost}&lcost=${lcost}`);
        }
        else
        {
        filterObj={sort:id,mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,location_id:location_id};
        this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${id}&hcost=${hcost}&lcost=${lcost}&location_id=${location_id}`);
        }
        axios(
            {
            method:'POST',
            url:'http://localhost:5000/restaurantfilter',
            headers:{'Content-Type':'application/json'},
            data:filterObj
            }
        )
        .then(res=>
        {
            this.setState({restaurants:res.data.restaurants})
        })
      }//end of 1st if
      else
      {
        if(location_id == undefined || location_id == 'Select')
        {
        filterObj={sort:id,mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5};
        this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${id}&hcost=${hcost}&lcost=${lcost}&cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}`);
        }
        else
        {
        filterObj={sort:id,mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,location_id:location_id,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5};
        this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&sort=${id}&hcost=${hcost}&lcost=${lcost}&location_id=${location_id}&cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}`);
        }
        axios(
            {
                method:'POST',
                url:`http://localhost:5000/cuisineHandler2`,
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
            )
        .then(res=>
            {
                this.setState({restaurants:res.data.restaurants});
            })
        .catch(err=>console.log(err))
      }//end of 1st else

    }

    
    handleDetail=(event)=>
    {       
        const location_id=event.target.id;
        const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        const mealtype_id=queryParams.mealtype_id;
        let filterObj={mealtype_id:mealtype_id,location_id:location_id};
        this.props.history.push(`/Details/?location_id=${location_id}&mealtype_id=${mealtype_id}`);
        
    }
   
    handleLocation=(event)=>
    {
        const location_id=event.target.value;
        const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        const mealtype_id=queryParams.mealtype_id;
        const hcost=queryParams.hcost ? queryParams.hcost : 10000;
        const lcost=queryParams.lcost ? queryParams.lcost : 1;
        const sort=queryParams.sort ? queryParams.sort : 1;
        const cuisine_id1=queryParams.cuisine_id1;
        const cuisine_id2=queryParams.cuisine_id2;
        const cuisine_id3=queryParams.cuisine_id3;
        const cuisine_id4=queryParams.cuisine_id4;
        const cuisine_id5=queryParams.cuisine_id5;
        let filterObj={};
      
      if (cuisine_id1 == undefined)
      {
        if (location_id == "Select")
        {
        this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&hcost=${hcost}&lcost=${lcost}&sort=${sort}`);
        filterObj={mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,sort:sort};
        }
        else
        {
            this.props.history.push(`/filter/?location_id=${location_id}&mealtype_id=${mealtype_id}&hcost=${hcost}&lcost=${lcost}&sort=${sort}`);
            filterObj={location_id:location_id,mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,sort:sort};
        }

        axios(
            {
                method:'POST',
                url:'http://localhost:5000/restaurantfilter',
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        )
        .then(res=>
            {
                this.setState({restaurants:res.data.restaurants})
            })
      }//end of 1st if
      else
      {
        if (location_id == "Select")
        {
        this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&hcost=${hcost}&lcost=${lcost}&sort=${sort}&cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}`);
        filterObj={mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,sort:sort,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5};
        }
        else
        {
            this.props.history.push(`/filter/?location_id=${location_id}&mealtype_id=${mealtype_id}&hcost=${hcost}&lcost=${lcost}&sort=${sort}&cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}`);
            filterObj={location_id:location_id,mealtype_id:mealtype_id,hcost:hcost,lcost:lcost,sort:sort,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5};
        }
        axios(
            {
                method:'POST',
                url:`http://localhost:5000/cuisineHandler2`,
                headers:{'Content-Type':'application/json'},
                data:filterObj
            }
        )
        .then(res=>
            {
                this.setState({restaurants:res.data.restaurants});
            })
        .catch(err=>console.log(err))
      }
    }

    handleCuisineChange=(event)=>
    {
        /*if there are 100 checkboxes then instead of cuisine_id1,cuisine_id2,3,4 etc. we should declare an array cuisineArray2
        and use a for loop of length 100 (i<100) and inside the loop say cuisineArray2[i]=cuisineArray[i] & if cuisineArray2[i] == undefined
        then cuisineArray2[i] == cuisineArray[0]. In the history.push give cuisine_id1=cuisineArray2[0]&cuisine_id2=cuisineArray2[1].....and so on
        all 100 cuisine_ids must be declared in URL. In the back-end & postman no change is needed */
        const {cuisineArray}=this.state;
        const cuisine_id=event.target.id;
        const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        const mealtype_id=queryParams.mealtype_id;
        const location_id=queryParams.location_id;
        const hcost=queryParams.hcost ? queryParams.hcost : 10000;
        const lcost=queryParams.lcost ? queryParams.lcost : 1;
        const sort=queryParams.sort ? queryParams.sort : 1;
        let filterObj={};
        let filterObj2={};
   
        if (cuisineArray.indexOf(cuisine_id) == -1)
        {
            cuisineArray.push(cuisine_id);
        }
        else
        {
            var index=cuisineArray.indexOf(cuisine_id);
            cuisineArray.splice(index,1);
        }   

        const cuisine_id1=cuisineArray[0];
        let cuisine_id2=cuisineArray[1];
        let cuisine_id3=cuisineArray[2];
        let cuisine_id4=cuisineArray[3];
        let cuisine_id5=cuisineArray[4];

        if (cuisineArray[1] == undefined)
        {
            cuisine_id2=cuisine_id1;
        }
        if (cuisineArray[2] == undefined)
        {
            cuisine_id3=cuisine_id1;
        }
        if (cuisineArray[3] == undefined)
        {
            cuisine_id4=cuisine_id1;
        }
        if (cuisineArray[4] == undefined)
        {
            cuisine_id5=cuisine_id1;
        }

        if (cuisineArray[0] != undefined)
        {
            if (location_id == undefined || location_id == "Select")
            {
                filterObj={mealtype_id:mealtype_id,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5,sort:sort,hcost:hcost,lcost:lcost};
                this.props.history.push(`/filter/?cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}&mealtype_id=${mealtype_id}&sort=${sort}&hcost=${hcost}&lcost=${lcost}`);
            }    
            else
            {
                filterObj={mealtype_id:mealtype_id,cuisine_id1:cuisine_id1,cuisine_id2:cuisine_id2,cuisine_id3:cuisine_id3,cuisine_id4:cuisine_id4,cuisine_id5:cuisine_id5,sort:sort,hcost:hcost,lcost:lcost,location_id:location_id};
                this.props.history.push(`/filter/?cuisine_id1=${cuisine_id1}&cuisine_id2=${cuisine_id2}&cuisine_id3=${cuisine_id3}&cuisine_id4=${cuisine_id4}&cuisine_id5=${cuisine_id5}&mealtype_id=${mealtype_id}&sort=${sort}&hcost=${hcost}&lcost=${lcost}&location_id=${location_id}`);
            }
    
            axios(
                {
                    method:'POST',
                    url:`http://localhost:5000/cuisineHandler2`,
                    headers:{'Content-Type':'application/json'},
                    data:filterObj
                }
            )
            .then(res=>
                {
                    this.setState({restaurants:res.data.restaurants});
                })
            .catch(err=>console.log(err))
        }
        else
        {
            if (location_id == undefined)
            {
                filterObj2={mealtype_id:mealtype_id,location_id:location_id,hcost:hcost,lcost:lcost,sort:sort}
                axios(
                    {
                        method:'POST',
                        url:'http://localhost:5000/restaurantfilter',
                        headers:{'Content-Type':'application/json'},
                        data:filterObj2
                    }
                )
                .then(res=>
                    {
                        this.setState({restaurants:res.data.restaurants})
                    })
                this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&hcost=${hcost}&lcost=${lcost}&sort=${sort}`);
            }
            else
            {
                filterObj2={mealtype_id:mealtype_id,location_id:location_id,hcost:hcost,lcost:lcost,sort:sort,location_id:location_id};
                axios(
                    {
                        method:'POST',
                        url:'http://localhost:5000/restaurantfilter',
                        headers:{'Content-Type':'application/json'},
                        data:filterObj2
                    }
                )
                .then(res=>
                    {
                        this.setState({restaurants:res.data.restaurants})
                    })
                this.props.history.push(`/filter/?mealtype_id=${mealtype_id}&hcost=${hcost}&lcost=${lcost}&sort=${sort}&location_id=${location_id}`);
            }
        }
    }

    render()
    {
        const {restaurants,totalPages,locations}=this.state;
        return(
            
                <div>
                    <div>
        <div id="myId" className="heading">Breakfast</div>
        <div className="container-fluid">
            <div className="row">
                <div className="col-sm-4 col-md-4 col-lg-4 filter-options">
                    <span className="glyphicon glyphicon-th-list toggle-span" data-toggle="collapse"
                        data-target="#demo"></span>
                    <div id="demo" className="collapse show">
                        <div className="filter-heading">Filters</div>
                        
                        <div className="Select-Location">Select Location</div>
                        
                        <select className="Rectangle-2236" onChange={this.handleLocation}>
                            <option>Select</option>
                        {locations.map(item=>{
                    return(<option value={`${item.location_id}`}>{item.location} </option>)})}  
                        </select>
                        <div className="Cuisine">Cuisine</div>
                            <input type="checkbox" name="cuisines" id="1" onChange={this.handleCuisineChange}  />
                            <span className="checkbox-items" > North indian </span> <div style={{display: "block"}}> </div>
                            <input type="checkbox" name="cuisines"  id="2" onChange={this.handleCuisineChange}  />
                            <span className="checkbox-items" > South indian </span> <div style={{display: "block"}}> </div>
                            <input type="checkbox" name="cuisines" id="3" onChange={this.handleCuisineChange}  />
                            <span className="checkbox-items" > Chinese</span> <div style={{display: "block"}}> </div>
                            <input type="checkbox" name="cuisines" id="4" onChange={this.handleCuisineChange} />
                            <span className="checkbox-items" > Fast Food </span> <div style={{display: "block"}}> </div>
                            <input type="checkbox" name="cuisines" id="5" onChange={this.handleCuisineChange}  />
                            <span className="checkbox-items" > Street Food </span> <div style={{display: "block"}}> </div>
                        
                        <div className="Cuisine">Minimum Price Range</div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="cost" onChange={()=>this.costChange('10','500')}/>
                            <span className="checkbox-items">Less than {'\u20B9'} 500</span>
                        </div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="cost" onChange={()=>this.costChange('500','1000')}/>
                            <span className="checkbox-items">{'\u20B9'} 500 to {'\u20B9'} 1000</span>
                        </div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="cost" onChange={()=>this.costChange('1000','1500')} />
                            <span className="checkbox-items">{'\u20B9'} 1000 to {'\u20B9'} 1500</span>
                        </div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="cost" onChange={()=>this.costChange('1500','2000')}/>
                            <span className="checkbox-items">{'\u20B9'} 1500 to {'\u20B9'} 2000</span>
                        </div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="cost" onChange={()=>this.costChange('2000','10000')}/>
                            <span className="checkbox-items">{'\u20B9'} 2000 +</span>
                        </div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="cost" onChange={()=>this.costChange('1','10000')}/>
                            <span className="checkbox-items">All</span>
                        </div>
                        <div className="Cuisine">Sort</div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="sort" id="100" onChange={()=>this.sortChange(1)}/>
                            <span className="checkbox-items">Price low to high</span>
                        </div>
                        <div style={{display: "block"}}>
                            <input type="radio" name="sort" id="101" onChange={()=>this.sortChange(-1)}/>
                            <span className="checkbox-items">Price high to low</span>
                        </div>
                    </div>
                </div>
                <div className="col-sm-8 col-md-8 col-lg-8">
                    {restaurants && restaurants.length > 0 ? restaurants && restaurants.map(item=> {
                    return (
                    <div className="Item">
                        <div className="row pl-1">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="img" src={breakfast} />
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest-name" id={item.location_id} onClick={this.handleDetail}>{item.name}</div>
                                <div className="res-location">FORT</div>
                                <div className="rest-address">{item.location}</div>
                            </div>
                        </div>
                        <hr />
                        <div className="row padding-left">
                            <div className="col-sm-12 col-md-12 col-lg-12">
                                <div className="rest-address"><div className="className1">CUISINES : {item.cuisine}</div></div>
                                <div className="rest-address"><div className="className1">COST FOR TWO : {'\u20B9'} {item.min_price} </div></div>
                            </div>
                        </div> 
                    </div>)}) : <div>No Data found</div>}
                    
                    <div className="pagination">
                        <a href="#">&laquo;</a>
                        {totalPages.map(item=>{
                            return <a href="#">{item}</a>
                            })}
                                                
                        <a href="#">&raquo;</a>
                    </div> 
                </div>
            </div>
        </div>        
    </div>
                </div>
            
        )
    }
}

export default withRouter(Filter);