import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import React from 'react';
import axios from 'axios';
import queryString from 'query-string';
import ReactDOM from 'react-dom';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import breakfast from '../images/breakfast.png';
import DemoCarousel from './DemoCarousel';
import Modal from 'react-modal';
import '../Styles/Filter.css';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};

class Details extends React.Component
{
    constructor()
    {
        super();
        this.state={
            disks:[],
            restaurants:[],
            location_id:undefined,
            isModalOpen:false,
            isLoginAlertModal:false,
            value:1,
            value100:0,
            arr:[],
            a:[],
            valueX:[],
            i:0,
            domainInputValue:0
        }
    }
    
   /* componentDidMount=()=>
    {
       axios(
           {
               method:'GET',
               url:'http://localhost:5000/hi/cityList',
               headers:{'Content-Type':'application/json'}
           }
       ).then(res=>
        {
            this.setState({disks:res.data.restaurants})
        })
        .catch(err=>console.log(err))
    } */

    componentDidMount=()=>
    {
        //var {Id}=this.props;
        const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        //const {Id}=this.props;
        const location_id=queryParams.location_id;
        const mealtype_id=queryParams.mealtype_id;
        const restaurant_id=queryParams.restaurant_id;
        const food_id=queryParams.food_id;
        //var mealtype_id=this.props.mealtypeId;
        //const cuisine_id='4';
        let filterObj={};
        if (mealtype_id != undefined)
        {
        filterObj={mealtype_id:mealtype_id,location_id:location_id};  //another key sort is used
        }
        else
        {
          filterObj={restaurant_id:restaurant_id};
        }

        axios(
            {
                method:'POST',   ///////////////////////////should be POST
                url:'http://localhost:5000/restaurantfilter',      //url is correct
                headers:{'Content-Type':'application/json'},
                data:filterObj  //data is always used for body Parser
            }
        ).then(res=>
            {
                this.setState({disks:res.data.restaurants})
            })
            .catch(err=>console.log(err))
    }

    handleClick=()=>
    {
      this.setState({isModalOpen:true});
    }
    
    increment=()=>
    {
      const {value}=this.state;
      this.setState({value:value+1});
    }

    decrement=(event,state)=>
    {
      const {value}=this.state;
      this.setState({value:value-1});
    }

    handleItem=(event,cost,food)=>
    {
      const queryParams=queryString.parse(this.props.location.search);
      const location_id=queryParams.location_id;
      const restaurant_id=queryParams.restaurant_id;
      const food_id=event.target.id;
      const restaurant_name=event.target.value;
      if (restaurant_id == undefined)
      {
      axios(
        {
          method:'GET',   ///////////////////////////should be POST
          url:`http://localhost:5000/food/${food_id}/${location_id}`,      //url is correct
          headers:{'Content-Type':'application/json'}
        }
      ).then(res=>
          {
              this.setState({restaurants:res.data.restaurants})
          })
          .catch(err=>console.log(err))
        } //end of if
        else
        {
          axios(
            {
              method:'GET',   ///////////////////////////should be POST
              url:`http://localhost:5000/food/${food_id}/${restaurant_id}`,      //url is correct
              headers:{'Content-Type':'application/json'}
            }
          ).then(res=>
              {
                  this.setState({restaurants:res.data.restaurants})
              })
              .catch(err=>console.log(err))
        } //end of else
      let count=sessionStorage.getItem('count');
      if (count == undefined || count.length <= 1)
      {
        
        this.setState({isLoginAlertModal:true});
      }
      else
      {
        this.setState({isModalOpen:true});
      }
    }

    cancel=()=>
    {
      this.setState({isModalOpen:false});
    }

    cancelLoginModal=()=>
    {
      this.setState({isLoginAlertModal:false});
    }

    qty=(event,state)=>
    {
      this.setState({[state]:event.target.value});
    }

    addToCart=(food_id,food_name,price,restaurant_id,restaurant)=>
    {
      const {value}=this.state;
      let value2=value;
      let value3=value2.toString();
      sessionStorage.setItem('value2',value3);
      let email=sessionStorage.getItem('email');
      const cost=price * value2;
      const item_cost=Number(price);
      let filterObj={email:email,food_id:food_id,food_name:food_name,item_cost:item_cost,cost:cost,qty:value2,restaurant_id:restaurant_id,restaurant:restaurant};
      let filterObj2={email:email,food_id:food_id,item_cost:item_cost};

      axios(
        {
          method:'POST',
          url:`http://localhost:5000/getFoodById`,
          headers:{'Content-Type':'application/json'},
          data:filterObj2
        }
      ).then(res=>
          {
              if (res.data.orders.length >= 1)
              {
                alert (`The selected item is already in your cart`);
              }
              else
              {
                axios(
                  {
                    method:'POST',
                    url:`http://localhost:5000/userCart`,
                    headers:{'Content-Type':'application/json'},
                    data:filterObj
                  }
                ).then(res=>
                    {
                        this.setState({restaurants:res.data.restaurants})
                    })
                    .catch(err=>console.log(err))  
                this.setState({value:1});
                this.setState({isModalOpen:false});
                alert("Item added to your cart successfully")
              }
          })
          .catch(err=>console.log(err))  
          
    }

    cancelOrder=()=>
    {
      const {value}=this.state;
      this.setState({value:1});
      this.setState({isModalOpen:false});
    }

    purchase=(price,food_id,food_name,qty,restaurant_id,restaurant)=>
    {
        const price1=Number(price);
        const {value}=this.state;
        const cost1=price1 * value;
        const cost=cost1.toString();
        sessionStorage.setItem('cost',cost);
        sessionStorage.setItem('foodIdFromCart',food_id);
        sessionStorage.setItem('foodNameFromCart',food_name);
        sessionStorage.setItem('itemCostFromCart',price);
        sessionStorage.setItem('qtyFromCart',qty);
        sessionStorage.setItem('restaurant_id',restaurant_id);
        sessionStorage.setItem('restaurant',restaurant);
        let filterObj={amount:Number(cost)};

       axios(
          {
            method:'POST',
            url:`http://localhost:5000/razorpay`,
            headers:{'Content-Type':'application/json'},
            data:filterObj
          }
        ).then(res=>
            {
                this.setState({restaurants:res.data.restaurants})
            })
            .catch(err=>console.log(err))  

        this.props.history.push('/payment4');
        //this.props.history.push('/payment2');
    }

    render()
    {
        const {disks,isModalOpen,value,value100,valueX,restaurants,isLoginAlertModal}=this.state;
        let {i}=this.state;
        return(
            <div>
             <DemoCarousel /> 
             <div>{disks.map(item=>{
               return(
                 <div className="rest-name" style={{fontSize:"350%",marginLeft:"20px"}}>
                   {item.name}
                   </div>)})}
                 </div>
      <div style={{marginBottom:"50px"}}>
            <Tabs style={{marginTop:"30px"}}>
    <TabList>
       <Tab> Home </Tab> 
       <Tab> Overview </Tab> 
       <Tab> Contact </Tab> 
    </TabList>
    
  <div style={{height:"250px"}}>
    <TabPanel style={{marginBottom:"10px"}}>
   {disks.map(item=> {
       return(
    <div>
      <div>{item.food && item.food.map(item2 => {
        return(
        <div>
         <div class="rest-name" style={{marginLeft:"20px"}}> <span>{item2.name}</span> </div>
          <div class="rest-name2"> <span>{'\u20B9'} {item2.cost}</span>  </div>
          <div>
             <button className="place-order" style={{marginLeft:"20px",padding:"5px",backgroundColor:"#00b300",color:"white",borderStyle:"none"}} type="button" id={item2.id} value={item.name} text={item2.name} onClick={(event)=>this.handleItem(event,`${item2.cost}`,`${item2.name}`)}>Place Order</button>
          </div>
        </div>)})}</div>
      
      
      </div>
       )})}   
      
    </TabPanel> 
    <TabPanel>
    {disks.map(item=> {  return(
      <div>
        <div class="rest-name" style={{marginLeft:"20px"}}> Cuisine : {item.cuisine} </div>
        <div class="rest-name" style={{marginLeft:"20px"}}> Melatype : {item.mealtype} </div>
        <div class="rest-name" style={{marginLeft:"20px"}}> Location : {item.location} </div>  
      </div>)})}
    </TabPanel>
    <TabPanel style={{marginBottom:"187px"}}>
      <div class="rest-name" style={{marginLeft:"20px"}}>Ph.no.1234567890</div>
    </TabPanel>
  </div>  
  </Tabs>
  </div>
  <Modal
          isOpen={isModalOpen}
          style={customStyles}  
          
        >
            
            <h3 style={{textAlign:"center"}}>Order Details</h3>
            <div className="modal-button5" style={{marginTop:"10px"}}>  <div style={{color:"green",position:"absolute",top:"98px",marginLeft:"-9px"}}>Qty</div> </div>
            {restaurants && restaurants.map(item => item.map(item2 => 
              {return(
            <div>
              
              <img src={breakfast} style={{width:"100px", height:"100px"}} />
              <div style={{color:"rgb(3, 83, 3)",fontSize:"110%"}}>{item2.name}</div>
              <div style={{color:"rgb(3, 83, 3)",fontSize:"110%"}}>{'\u20B9'} {item2.cost * this.state.value}</div> 
              
            </div>   )}))}
            
            {restaurants && restaurants.map(item => item.map(item2 =>
              {return(
              <div>
                <span>
                
                <div className="modal-button3"> <button className="button-style" type="button" id={item2.id} value={value} text={item2.qty} onClick={this.increment}>+</button>  </div>
              </span>
              <div className="modal-button5">  <span style={{color:"green"}}>{value}</span> </div>
              
              <span>
                <div className="modal-button4"> {this.state.value == 1 ? <button className="button-style" type="button" id={item2.id} value={value} text={item2.qty} disabled="true" onClick={this.decrement}>-</button>
                                                    : <button className="button-style" type="button" id={item2.id} value={value} text={item2.qty} onClick={this.decrement}>-</button> } </div>

            </span>
              
          <div style={{marginTop:"95px"}}>
            <div className="modal-button6">  <button className="btn btn-sm btn-primary login-button" style={{paddingLeft:"82px",paddingRight:"79px",backgroundColor:"rgb(80, 80, 124)"}} id={item2.cost} onClick={()=>this.purchase(`${item2.cost}`,`${item2.id}`,`${item2.name}`,value,`${item2.restaurant_id}`,`${item2.restaurant}`)}>Buy Now</button>  </div>
            <div className="modal-button6"> <button className="btn btn-sm btn-primary login-button" style={{paddingLeft:"82px",paddingRight:"66px",marginTop:"10px",backgroundColor:"orange",borderColor:"orange"}} onClick={()=>this.addToCart(`${item2.id}`,`${item2.name}`,`${item2.cost}`,`${item2.restaurant_id}`,`${item2.restaurant}`)}>Add to Cart</button>  </div>
            <div className="modal-button6"> <button className="btn btn-sm btn-primary login-button" style={{paddingLeft:"82px",paddingRight:"93px",textAlign:"center",marginTop:"10px",backgroundColor:"red",borderColor:"red"}} onClick={this.cancelOrder}>Cancel</button>  </div>
          </div>
          </div> )}))}
        </Modal>

        <Modal
          isOpen={isLoginAlertModal}
          style={customStyles}  
          
        >
          <h5>Please login to proceed further</h5>
            <button className="btn btn-sm btn-primary"  onClick={this.cancelLoginModal}>Cancel</button>   
           
        </Modal>
  
  </div>  
        )

    }    
}

export default Details;