import axios from 'axios';
import Select from 'react-select';
import React,{useState} from 'react';
//import{withRouter} from 'react-router-dom';
import '../Styles/Filter.css';
import queryString from 'query-string';
import Details from './Details';
import breakfast from '../images/breakfast.png';
import Location from './Location';
import header from './Header';

class ordersPage extends React.Component
{
    constructor()
    {
        super();
        this.state={
            orders:[]
           // qty:0      
        }        
    }
    
    componentDidMount=(event)=>
    {
        const email=sessionStorage.getItem('email');
        let filterObj={email:email};
        axios(
            {
              method:'POST',
              url:`http://localhost:5000/userCartDetails`,
              headers:{'Content-Type':'application/json'},
              data:filterObj
            }
          ).then(res=>
              {
                  this.setState({orders:res.data.orders})
              })
              .catch(err=>console.log(err))
    }   

    render()
    {
        const {orders,costArray,i}=this.state;
        let count=sessionStorage.getItem('count');
        const order_id=sessionStorage.getItem('order_id');
        const txn_id=sessionStorage.getItem('txn_id');

        return(
            
        <div>
            {count.length >= 2 ? 
            <div className="container">
                <div className="row">
                <div className="col-sm-8 col-md-8 col-lg-8">
                    {orders && orders.length > 0 ? orders && orders.map(item=> item.orders_original.map(item2 => {
                    return (
                    <div className="Item">
                        <div className="row pl-1">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="img" src={breakfast} />
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest-name">{item2.food_name}</div>
                                <div className="res-location">Item Price : {'\u20B9'} {item2.item_cost}</div>
                                <div className="res-location">Quantity : {item2.qty}</div>
                            </div>
                        </div>
                        <hr />
                        <div className="row padding-left">
                            <div className="col-sm-12 col-md-12 col-lg-12">
                                <div className="rest-address"><div className="className1">TOTAL COST : {'\u20B9'} {item2.cost} <span>Order ID : {item2.order_id}</span></div></div>
                                <div className="rest-address"><div className="className1">Restaurant : {item2.restaurant} <span>Transaction ID : {item2.txn_id}</span> </div></div>   
                            </div>
                        </div> 
                    </div>)})) : <div></div>}
                </div>
                {orders && orders.map(item=>item.orders_original.length > 0  ? <div></div> : <div> <h4 style={{textAlign:"center"}}>No Orders found</h4> </div> )}
            </div>
            </div> : <div style={{textAlign:"center",fontSize:"150%"}}>Please login and then click on "My Orders" to view your orders</div> }
        </div>            
        )
    }   
}

export default ordersPage;