import React from 'react';
import'../Styles/Home.css';
import{withRouter} from 'react-router-dom';
//import axios from 'axios';
import bg from '../images/bg.png';
import axios from 'axios';
import queryString from 'query-string';

class Wallpaper extends React.Component
{
    constructor()
    {
        super();
        this.state={
            restaurants:[],
            suggestions:[],
            text:''
        }
    }
   handleLocation=(event)=>
   {
       const location_id=event.target.value;
       this.props.history.push(`/?location_id=${location_id}`);
       axios(
        {
            method:'GET',
            url:`http://localhost:5000/restaurantList/${location_id}`,
            headers:{'Content-Type':'application/json'}
        }
    ).then(res=>
     {
         this.setState({restaurants:res.data.restaurants})
     })
     .catch(err=>console.log(err))

       //sessionStorage.setItem('location_id',location_id);
   }

   handleChange=(event,state)=>
   {
    
    
    const value={[state]:event.target.value};
    const mail=event.target.value;
      //this.setState(value);
      const {restaurants}=this.state;
      

      let suggestions=[];
      
      if(mail.length > 0)
      {
        suggestions=restaurants.filter(item=> item.name.toLowerCase().includes(mail.toLowerCase()));
      }
      this.setState(()=>({suggestions:suggestions,text:mail}))  
   }

   renderSuggestions=()=>
   {
       const {suggestions}=this.state;
       const {restaurants}=this.state;
       const queryParams=queryString.parse(this.props.location.search); //props.location.search reads data from another file
        const location_id=queryParams.location_id;
        if(suggestions.length === 0)
        {
            return null;
        }
        else
        {
            return(
            <div className="container">   
              <div className="row">
                <div className="col-sm-6"></div>
                <div className="col-sm-6 box2">
                <ul class="ul-list">
                    {
                        restaurants.map((item => (<li class="restaurantList" id={item.restaurant_id} onClick={(event)=>this.selectedText(event,`${item.restaurant_id}`)}> {item.name} </li>)))
                    }
                </ul>
                </div>
              </div>
            </div>   
            )
        }
      
   }

   selectedText=(event,state)=>
   {
       const mail={[state]:event.target.id};
       const value=event.target.id;
        this.setState({text:value,suggestions:[]})
        this.props.history.push(`/Details/?restaurant_id=${value}`);
   }

    render()
    {
        const {locations}=this.props;
        const {value,text,suggestions,restaurants}=this.state;
        return(
            <div>
                <div><img src={bg} style={{width:'100%',height:'500px'}}/></div>
                <div className="circle"> </div>
                <div id="letter">
                    e!
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-sm-12 col-md-12 col-lg-12">
                            <div className="heading">
                                <div className="class2">Find the best restaurants, cafes and bars</div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-6">   
                            <select className="box1" onChange={this.handleLocation}>
                            <option>Select location</option>
                                    {locations.map((item)=>{
                                        return <option value={item.location_id}>{item.location}</option>  //below box1 be changed to location Dropdown
                                    })}
                            </select>
                        </div>
                        <div className="col-sm-6">
                            <input className="box2" type="text" value={text} onChange={(event)=>this.handleChange(event,'text')} placeholder="Search for restaurants"/>
                            {this.renderSuggestions()}
                            <div className="search_symbol">
                                <div className="class3"><span className="glyphicon glyphicon-search"></span></div>
                            </div>
                        </div>
                    </div>
                </div>    
            </div>
        )
    }
}

export default withRouter(Wallpaper);//replace Home with Wallpaper