import axios from 'axios';
import Select from 'react-select';
import React,{useState} from 'react';
//import{withRouter} from 'react-router-dom';
import '../Styles/Filter.css';
import queryString from 'query-string';
import Details from './Details';
import breakfast from '../images/breakfast.png';
import Location from './Location';
import header from './Header';

class cartPage2 extends React.Component
{
    constructor()
    {
        super();
        this.state={
            orders:[],
            value:1   
        }        
    }
    
    componentDidMount=()=>
    {
        const email=sessionStorage.getItem('email');
        let filterObj={email:email};
        axios(
            {
              method:'POST',
              url:`http://localhost:5000/userCartDetails`,
              headers:{'Content-Type':'application/json'},
              data:filterObj
            }
          ).then(res=>
              {
                  this.setState({orders:res.data.orders})
              })
              .catch(err=>console.log(err))
    }   

    purchase=(price1,qty2,food_id,food_name,restaurant_id,restaurant)=>
    {
        const price=Number(price1) * Number(qty2);
        const cost=price.toString();
        sessionStorage.setItem('cost',cost);
        sessionStorage.setItem('foodIdFromCart',food_id);
        sessionStorage.setItem('foodNameFromCart',food_name);
        sessionStorage.setItem('itemCostFromCart',price1);
        sessionStorage.setItem('qtyFromCart',qty2);
        sessionStorage.setItem('restaurant_id',restaurant_id);
        sessionStorage.setItem('restaurant',restaurant);
        let filterObj={amount:cost};

        axios(
          {
            method:'POST',
            url:`http://localhost:5000/razorpay`,
            headers:{'Content-Type':'application/json'},
            data:filterObj
          }
        ).then(res=>
            {
                this.setState({restaurants:res.data.restaurants})
            })
            .catch(err=>console.log(err))  
            
        this.props.history.push('/payment4');
        //this.props.history.push('/payment2');
    }

    deleteCartItem=(food_id,item_cost2)=>
    {
        const email=sessionStorage.getItem('email');
        const item_cost=Number(item_cost2);
        let filterObj={email:email,food_id:food_id,item_cost:item_cost};
        let filterObj2={email:email};
        axios(
            {
              method:'POST',
              url:`http://localhost:5000/deleteCartItem`,
              headers:{'Content-Type':'application/json'},
              data:filterObj
            }
          ).then(res=>
              {
                  this.setState({orders:res.data.orders});
                  axios(
                    {
                        method:'POST',
                        url:`http://localhost:5000/userCartDetails`,
                        headers:{'Content-Type':'application/json'},
                        data:filterObj2
                    }
                    ).then(res=>
                        {
                            this.setState({orders:res.data.orders})
                        })
                        .catch(err=>console.log(err))
              })
              .catch(err=>console.log(err))

        
    }

    increment=(qty,food_id,item_cost2)=>
    {
      const {value,orders}=this.state;
      const email=sessionStorage.getItem('email');
      const inc=1;
      const qty2=Number(qty);
      const item_cost=Number(item_cost2);
      

      let filterObj={email:email,food_id:food_id,qty:qty2,inc:inc,item_cost:item_cost};
      let filterObj2={email:email};

        axios(
        {
            method:'POST',
            url:`http://localhost:5000/updateQty`,
            headers:{'Content-Type':'application/json'},
            data:filterObj
        }
        ).then(res=>
            {
                this.setState({orders:res.data.orders})
            })
            .catch(err=>console.log(err))

        axios(
            {
                method:'POST',
                url:`http://localhost:5000/userCartDetails`,
                headers:{'Content-Type':'application/json'},
                data:filterObj2
            }
            ).then(res=>
                {
                    this.setState({orders:res.data.orders})
                })
                .catch(err=>console.log(err))
        
    } 

    decrement=(qty,food_id,item_cost)=>
    {
      const {value,orders}=this.state;
      const email=sessionStorage.getItem('email');
      const dec=1;
      const qty2=Number(qty);
      

      let filterObj={email:email,food_id:food_id,qty:qty2,dec:dec,item_cost:item_cost};
      let filterObj2={email:email};

      axios(
        {
            method:'POST',
            url:`http://localhost:5000/updateQty`,
            headers:{'Content-Type':'application/json'},
            data:filterObj
        }
        ).then(res=>
            {
                this.setState({orders:res.data.orders})
            })
            .catch(err=>console.log(err))

    axios(
        {
            method:'POST',
            url:`http://localhost:5000/userCartDetails`,
            headers:{'Content-Type':'application/json'},
            data:filterObj2
        }
        ).then(res=>
            {
                this.setState({orders:res.data.orders})
            })
            .catch(err=>console.log(err))
      
    }   

    render()
    {
        const {orders,costArray,i,value}=this.state;
        let qty2=sessionStorage.getItem('value2');
        let count=sessionStorage.getItem('count');

        return(
            
        <div>
            {count.length > 1 ? 
            <div className="container">
                <div className="row">
                <div className="col-sm-8 col-md-8 col-lg-8">
                    {  orders && orders.length > 0 ? orders &&  orders.map(item=> item.orders.map(item2 => {
                    return (  
                    <div className="Item" style={{height:"243px"}}>
                        <div className="row pl-1">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="img" src={breakfast} />
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest-name" id={item2.food_id} onClick={this.handleDetail}>{item2.food_name}</div>
                                <div className="res-location">Item Price : <span className="item_price">{'\u20B9'} {item2.item_cost} </span> </div>
                                <div className="res-location">Quantity : </div>
                                
                                <span>
                
                <div className="cartButton"> <button className="cartButton-decAndInc" type="button" id={item2.id} value={value} text={item2.qty} onClick={()=>this.increment(`${item2.qty}`,`${item2.food_id}`,`${item2.item_cost}`)}>+</button>  </div>
              </span>
              <div className="qty-style">  <span style={{color:"green"}}>{item2.qty}</span> </div>
              
              <span>
                <div className="cartButton-dec"> {item2.qty == 1 ? <button className="cartButton-decAndInc" type="button" id={item2.id} value={value} text={item2.qty} disabled="true" onClick={this.decrement}>-</button>
                                                    : <button className="cartButton-decAndInc" type="button" id={item2.id} value={value} text={item2.qty} onClick={()=>this.decrement(`${item2.qty}`,`${item2.food_id}`,`${item2.item_cost}`)}>-</button> } </div>

                    </span>     
                                <div> <button type="button" className="order-now" id={item2.cost} value={item2.qty} onClick={()=>this.purchase(`${item2.item_cost}`,`${item2.qty}`,`${item2.food_id}`,`${item2.food_name}`,`${item2.restaurant_id}`,`${item2.restaurant}`)}> <h6>ORDER NOW</h6> </button>  </div>
                                <div style={{marginTop:"-45px",marginLeft:"125px"}}> <button type="button" style={{color:"white",backgroundColor:"green", marginTop:"10px", borderColor:"green",border:"none"}} onClick={()=>this.deleteCartItem(`${item2.food_id}`,`${item2.item_cost}`)}> <h6>Remove from Cart</h6> </button>  </div>
                                {/*<button type="button" onClick={this.increment}> + </button>
                                <span>{qty}</span>
                                <button type="button" onClick={this.decrement}> - </button>*/}
                            </div>
                        </div>
                        <hr />
                        <div className="row padding-left">
                            <div className="col-sm-12 col-md-12 col-lg-12">
                                <div className="rest-address"><div className="className1">TOTAL COST : {'\u20B9'} {item2.item_cost * item2.qty} </div></div>
                                <div className="rest-address"><div className="className1">Restaurant : {item2.restaurant} </div></div>
                            </div>
                        </div> 
                            </div>  )}))     : <div> </div> }
                </div> 
                {orders && orders.map(item=>item.orders.length > 0  ? <div></div> : <div> <h4 style={{textAlign:"center"}}>Your Cart is Empty</h4> </div> )}
            </div>
              
            </div> : <div style={{textAlign:"center",fontSize:"150%"}}>Please login and then click on cart to view your saved items</div> }
        </div>            
        )
    }   
}

export default cartPage2;