import React from 'react';
import '../Styles/index.css';
import logo from '../images/breakfast.png';

function loadScript(src)
{
    return new Promise( (resolve) =>{
    const script=document.createElement('script')
    script.src=src
    
    script.onload=()=>
    {
        resolve(true)
    }
    script.onerror=()=>
    {
        resolve(false)
    }
    document.body.appendChild(script)
  })
}

const __DEV__ = document.domain === 'localhost'

function App()
{
    async function displayRazorpay ()
    {
        const res=await loadScript('https://checkout.razorpay.com/v1/checkout.js')

        if (!res)
        {
            alert("Razorpay failed to load")
            return
        }

        const cost=sessionStorage.getItem('cost');

        const data= await fetch('http://localhost:5000/razorpay', {method:'POST'}).then(t=>
            t.json()
        )

        console.log(data)

        
        const options = {
            "key": __DEV__ ? "rzp_test_s78Xda7aNut55E" : "NO_API", // Enter the Key ID generated from the Dashboard
            "currency":data.currency,
            "amount":Number(cost),
            "order_id":data.id,
            "TXN_ID":new Date().getTime(),
            "name": "Acme Corp",
            "description": "Test Transaction",
            "image": "https://example.com/your_logo",
             //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
             handler : function (response) 
                        {
                            let txn_id=new Date().getTime().toString();
                            sessionStorage.setItem('payment_id',response.razorpay_payment_id);
                            sessionStorage.setItem('order_id',response.razorpay_order_id);
                            sessionStorage.setItem('signature',response.razorpay_signature);
                            sessionStorage.setItem('txn_id',txn_id);
                            window.location.href="http://localhost:3000/payment-status"
                        },
            "callback_url": "https://eneqd3r9zrjok.x.pipedream.net/",
            "prefill": {
                "name": "Gaurav Kumar",
                "email": "gaurav.kumar@example.com",
                "contact": "9999999999"
            },
            "notes": {
                "address": "Razorpay Corporate Office"
            },
            "theme": {
                "color": "#3399cc"
            }
        };
        
        const paymentObject = new window.Razorpay(options);
        paymentObject.open();
    }
    const cost=sessionStorage.getItem('cost');
    const item=sessionStorage.getItem('foodNameFromCart');
    const qty=Number(sessionStorage.getItem('qtyFromCart'));
    const restaurant=sessionStorage.getItem('restaurant');

    return(
        <div>
            <div>Item : {item}</div>
            <div>Qty : {qty}</div>
            <div>Restaurant : {restaurant}</div>
            <div>Amount : {'\u20B9'} {cost}</div>
            <button onClick={displayRazorpay}>Buy Now</button>
            <button>Cancel</button>
        </div>
    )
}

export default App