import React from 'react';
import '../Styles/Filter.css';
import Modal from 'react-modal';
import{withRouter} from 'react-router-dom';
import axios from 'axios';

const customStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)'
    }
  };


class Header2 extends React.Component
{
    constructor()
    {
        super();
        this.state=
        {
            signUpModalIsOpen:false,
            loginModalIsOpen:false,
            errorLoginModalIsOpen:false,
            email:'',
            password:'',
            FN:'',
            LN:undefined,
            i:0,
            users:[]
        }
    }

    handleLogo=()=>
    {
        this.props.history.push('/');
    }

    logout=()=>
    {
        this.setState({loginModalIsOpen:true});
    }

    render()
    {
        const {signUpModalIsOpen,loginModalIsOpen,email,password,FN,LN,users,errorLoginModalIsOpen}=this.state;
        return(
            <div>
                <div className="band"></div>
        <button type="button" className="login" onClick={this.logout}>Logout</button>
        <div className="logo" onClick={this.handleLogo}>
               <div className="letter" onClick={this.handleLogo}> <h2 onClick={this.handleLogo}>e!</h2> </div>
            
        </div>
        
        </div>
        )
    }
}

export default withRouter(Header2);