import React from 'react';
import'../Styles/Home.css';
import axios from 'axios';

class paymentStatus extends React.Component
{
    componentDidMount=()=>
    {
        const email=sessionStorage.getItem('email');
       const food_id=sessionStorage.getItem('foodIdFromCart');
       const food_name=sessionStorage.getItem('foodNameFromCart');
       const item_cost=Number(sessionStorage.getItem('itemCostFromCart'));
       const cost=Number(sessionStorage.getItem('cost'));
       const qty=Number(sessionStorage.getItem('qtyFromCart'));
       const restaurant_id=sessionStorage.getItem('restaurant_id');
       const restaurant=sessionStorage.getItem('restaurant');
       const dateAndTime=new Date().toLocaleString();
       const order_id=sessionStorage.getItem('order_id');
        const signature=sessionStorage.getItem('signature');
        const payment_id=sessionStorage.getItem('payment_id');
        const txn_id=sessionStorage.getItem('txn_id');

       let filterObj={email:email,food_id:food_id,food_name,item_cost:item_cost,cost:cost,qty:qty,restaurant_id:restaurant_id,restaurant:restaurant,dateAndTime:dateAndTime,
                        order_id:order_id,signature:signature,payment_id:payment_id,txn_id:txn_id};
       let filterObj2={email:email,food_id:food_id,food_name,item_cost:item_cost,cost:cost,qty:qty,restaurant_id:restaurant_id,restaurant:restaurant};

       axios(
        {
          method:'POST',
          url:`http://localhost:5000/userOrders`,
          headers:{'Content-Type':'application/json'},
          data:filterObj
        }
      ).then(res=>
          {
              this.setState({orders:res.data.orders})
          })
          .catch(err=>console.log(err))
        axios(
        {
            method:'POST',
            url:`http://localhost:5000/deleteCartItem`,
            headers:{'Content-Type':'application/json'},
            data:filterObj2
        }
        ).then(res=>
            {
                this.setState({orders:res.data.orders})
            })
            .catch(err=>console.log(err))
    }
   
    render()
    {
        const order_id=sessionStorage.getItem('order_id');
        const signature=sessionStorage.getItem('signature');
        const payment_id=sessionStorage.getItem('payment_id');
        const txn_id=sessionStorage.getItem('txn_id');
        return(
            <div>
                <h2 style={{color:"green",textAlign:"center"}}>PAYMENT SUCCESSFUL</h2>
                <div style={{marginLeft:"20px",fontSize:"150%"}}>
                    ORDER ID : {order_id}
                </div>
                <div style={{marginLeft:"20px",fontSize:"150%"}}>
                    Payment ID : {payment_id}
                </div>
                <div style={{marginLeft:"20px",fontSize:"150%"}}>
                    Transaction ID : {txn_id}
                </div>
            </div>
        )
    }
}

export default paymentStatus;