import React from 'react';
import {BrowserRouter,Route} from 'react-router-dom';
import Home from '../src/Components/Home';
import Filter from '../src/Components/Filter';
import Details from '../src/Components/Details';
import Header from '../src/Components/Header';
import payment from '../src/Components/payment';
import cartPage2 from '../src/Components/cartPage2';
import ordersPage from '../src/Components/ordersPage';
import payment2 from '../src/Components/payment2';
import payment4 from '../src/Components/payment4';
import paymentStatus from '../src/Components/paymentStatus';

const Router=()=>
{
    return(
        <BrowserRouter>
            <Header/>
            <Route exact path="/" component={Home}/>
            <Route path="/filter" component={Filter}/>
            <Route path="/Details" component={Details}/>
            
            <Route path="/payment" component={payment} /> 
            <Route path="/cartPage2" component={cartPage2} />
            <Route path="/ordersPage" component={ordersPage} />
            <Route path="/payment2" component={payment2} />
            <Route path="/payment4" component={payment4} />
            <Route path="/payment-status" component={paymentStatus} />
        </BrowserRouter>
    )
}

export default Router;