import React from 'react';

class checking2 extends React.Component
{
    constructor()
    {
        super();
        this.state={
            value:0
        }
    }

  increment=()=>
  {
      const {value}=this.state;
      this.setState({value:value+1})
  }

  decrement=(event)=>
  {
      this.setState({value:event.target.id-1})
  }

    render()
    {
        const {value}=this.state;
        return(
            <div>
               <button type="button" id={value} onClick={this.increment}>+</button> 
               <span>{this.state.value}</span>
               <button type="button" id={value} onClick={this.decrement}>-</button>
            </div>
        )
    }
}

export default checking2;