import React from 'react';
import axios from 'axios';
import breakfast from '../images/breakfast.png';
import queryString from 'query-string';

class cart extends React.Component
{
    constructor()
    {
        super();
        this.state={
            carts:[],
            value:0
        }
    }

    increment=()=>
    {
        this.setState({value:this.state.value+1});
    }

    componentDidMount=()=>
    {
        const queryParams=queryString.parse(this.props.location.search);
        const restaurant_id=queryParams.restaurant_id;
        const food_id=queryParams.food_id;
        const cost=queryParams.cost;
        const restaurant_name_dummy=queryParams.restaurant_name;
        const food=queryParams.food;
        const rest=restaurant_name_dummy.split('%20')[0];
        const rest2=restaurant_name_dummy.split('%20')[1];
        const restaurant_name=rest + ' ' + rest2;
        let filterObj={restaurant_id:restaurant_id,id:food_id,cost:cost,restaurant:restaurant_name,food:food};
        axios({
            
            method:'POST',   ///////////////////////////should be POST
            url:'http://localhost:5000/postcart',      //url is correct
            headers:{'Content-Type':'application/json'},
            data:filterObj
        })
        .then(res=>
            {
                this.setState({carts:res.data.carts})
            }
        )
    }

    render()
    {
        const {carts}=this.state;
        return(
            
            <div className="col-sm-8 col-md-8 col-lg-8">
                    {carts && carts && carts.map(item=> {
                    return (
                    <div className="Item">
                        <div className="row pl-1">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="img" src={breakfast} />
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest-name" >{item.food}</div>
                                <div className="res-location">{item.restaurant}</div>
                                <button type="button" onClick={this.increment}>+</button>
                                <span>{this.state.value}</span>
                                <button type="button" onClick={this.decrement}>-</button>
                            </div>
                        </div>
                        <hr />
                        <div className="row padding-left">
                            <div className="col-sm-12 col-md-12 col-lg-12">
                                
                                <div className="rest-address"><div className="className1">COST : {item.cost}*{this.state.value} </div></div>
                            </div>
                        </div> 
                    </div>)})}
            </div>          
        )
    }
}

export default cart;